.weather-header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  padding: 1rem;
  margin-bottom: 1rem;
  text-align: center;

  border-width: 1px;
  border-style: solid;
  border-top: unset;
  border-radius: 0 0 10px 10px;
}

.weather-header > .weather-search {
  margin: auto 0;
}

.weather-header > .controls {
  display: flex;
  flex-wrap: wrap;
}
